.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--body-color);
  z-index: var(--z-fixed);
  transition: 0.4s;
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: var(--header-height);
}
.nav__logo {
  color: var(--first-color);
  font-weight: var(--font-medium);
  transition: 0.4s;
}
.nav__logo:hover {
  color: var(--first-color-alt);
}
.nav__menu {
  position: fixed;
  bottom: 1rem;
  background-color: hsl(var(--second-hue), 32%, 37%, 0.9);
  left: 1rem;
  right: 1rem;
  padding: 1rem 2rem;
  border-radius: 3rem;
  backdrop-filter: blur(10px);
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__link {
  color: var(--text-color);
  font-size: 1.3rem;
  padding: 0.4rem;
  display: flex;
  align-items: center;
  border-radius: 5rem;
}

.active__link {
  background: linear-gradient(
    180deg,
    hsla(var(--first-hue), var(--saturation), var(--lightness), 1),
    hsla(var(--first-hue), var(--saturation), var(--lightness), 0.2)
  );
  box-shadow: 0 0 16px
    hsla(var(--first-hue), var(--saturation), var(--lightness), 0.4);
  border-radius: 50%;
  color: var(--title-color);
}

@media screen and (max-width: 968px) {
  .nav__text {
    display: none;
  }
}
